export default new class BasicPerceptronModel {

  constructor() {
    this.k = 0;
    this.b = 0;
  }

  evaluateActivation(v) {
    return 1 / (1 + Math.exp(-v));
  }

  getClassificationLineY(x) {
    return this.k * x + this.b;
  }

  evaluate(x, y) {
    return this.evaluateActivation(this.getClassificationLineY(x) - y);
  }

  evaluateCost(x, y, category) {
    return this.evaluate(x, y) - category;
  }

  predict(x, y) {
    return Math.sign(this.evaluate(x, y));
  }

  evaluateLoss(examples) {
    return examples.reduce((acc, v) => acc + this.evaluateCost(v.x, v.y, v.category), 0)
  }

}